<template>
    <div class="wrap work-report">
        <el-form ref="form" label-width="84px" size="small">
            <el-form-item label="报告类型：">
                <el-select v-model="queryForm.reportType" @change="handleFilter">
                    <el-option label="日报" value="日报"></el-option>
                    <el-option label="月报" value="月报"></el-option>
                    <el-option label="周报" value="周报"></el-option>
                </el-select>
            </el-form-item>
            <el-button type="primary" size="small" @click="dialogAddVisible = true">新增工作报告</el-button>
        </el-form>
        <el-table class="table" :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" max-height="500" border style="width: 100%; margin-top: 20px;" 
            :header-cell-style="{'text-align': 'center'}" :cell-style="{'text-align': 'center'}"
            :default-sort = "{prop: 'date', order: 'descending'}">
            <el-table-column prop="date" sortable label="日期">
                <template slot-scope="scope">{{formatDate(new Date(scope.row.date))}}</template>
            </el-table-column>
            <el-table-column prop="report_type" label="报告类型"></el-table-column>
            <el-table-column prop="summary" label="总结"></el-table-column>
            <el-table-column prop="" label="操作">
                <template slot-scope="scope">
                    <a class="text-base" style="margin-right: 10px;">编辑</a>
                    <a class="text-base" @click="handleDel(scope.$index)">删除</a>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->
        <el-pagination v-if="tableData.length" background layout="total,prev, pager, next"
            :page-size="pagesize"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :total="tableData.length" style="float: right;">
        </el-pagination>
        <!-- 新增工作报告模态框 -->
        <el-dialog title="新增工作报告" :visible.sync="dialogAddVisible" :close-on-click-modal='false' width="550px">
            <el-form :inline="true" :model="formAdd" ref="formAdd">
                <el-form-item style="margin-right: 76px" label="类型：" prop="report_type">
                    <el-select v-model="formAdd.report_type" placeholder="请选择" size="small" style="width: 100px">
                        <el-option label="日报" value="日报"></el-option>
                        <el-option label="周报" value="周报"></el-option>
                        <el-option label="月报" value="月报"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item prop="date">
                    <el-date-picker
                        v-model="formAdd.date"
                        type="date"
                        size="small"
                        format="yyyy-MM-dd"
                        placeholder="选择日期时间">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="总结：" prop="summary">
                    <el-input type="textarea" v-model="formAdd.summary" placeholder="" :rows="4" style="width: 401px;"></el-input>
                </el-form-item> 
                <el-form-item label="计划：" prop="plan">
                    <el-input type="textarea" v-model="formAdd.plan" placeholder="" :rows="4" style="width: 401px;"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddVisible = false">取 消</el-button>
                <el-button type="primary" @click="workReportSave">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import { mapState, mapActions, mapMutations } from 'vuex'
    export default {
        data() {
            return {
                num: 1,
                activeName: 'first',                                        //  tab切换
                queryForm: {
                    reportType: ''
                },                                             //  日志类型
                tableData: [],                                              //  表格数据
                currentPage: 1,                                             //默认开始页面
                pagesize: 2,                                                //每页的数据条数
                dialogAddVisible: false,                                    //  新增报告模态框
                formAdd: {
                    report_type: '日报',
                    date: new Date(),
                    summary: '',
                    plan: '',
                },                                                //  新增报告表单
                numberValidateForm: {
                    age: ''
                }
            }
        },
        activated(){
            /**
             * 页面加载前获取所有工作报告数据
             */
            this.initCrmWorkReport = function(){
                this.$axios.post(this.$store.state.crmPath+"/crmWorkReportPR/queryCrmWorkReport").then(res => {
                    this.tableData = res.data;
                    console.log(res.data)
                })
            };

            this.initCrmWorkReport();
        },
        methods: {  
            /**
             * 报告类型筛选
             */
            handleFilter(value){
                let param = new URLSearchParams();
                param.append('data', JSON.stringify(this.queryForm));
                this.$axios.post(this.$store.state.crmPath+"/crmWorkReportPR/queryCrmWorkReport", param).then(res => {
                    console.log(res.data)
                })
            },
            /**
             * 表格页面跳转
             */
             handleCurrentChange(val) {
                this.currentPage = val;
            },
            /**
             * 新增工作报告
             */
            workReportSave(){
                let param = new URLSearchParams();
                const data = this.formAdd;
                param.append('data', JSON.stringify(data));
                this.$refs['formAdd'].resetFields();
                this.$axios.post(url,param).then(res => {
                    this.$notify({ message: '操作成功！', position: 'bottom-right'});
                    this.tableData.unshift(this.deepClone(data));
                    this.dialogAddVisible = false;
                    this.$refs['formAdd'].resetFields();
                }).then(res => {
                    console.log(3);
                })
            },
            /**
             * 删除操作
             */
            handleDel(index){
                this.tableData.splice(index,1);
                const h = this.$createElement;
                this.$msgbox({
                    title: '提示',
                    message: "确定要删除【xxxx】吗？",
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(action => {
                    console.log(2)
                }).catch(()=>{});
            },
        }
    }
</script>
<style lang="less" scoped>
    .tab-item{
        display: block;
        width: 140px;
        text-align: center;
    }
    .el-tabs{
        background: #fff;
    }
    .el-tab-pane{
        background: #fafafa;
    }
    .first .el-form, .second .el-form{
        padding: 30px 0 10px;
    }   
</style>




